@import url(https://fonts.googleapis.com/css?family=Raleway:500);
@font-face {
  font-family: EuclidBold;
  src: url("/static/media/Euclid Circular A Bold.b4788f2f.ttf");
}

@font-face {
  font-family: ManropeMedium;
  src: url(/static/media/Manrope-Medium.4a88d55f.ttf);
}

@font-face {
  font-family: ManropeSemiBold;
  src: url(/static/media/Manrope-SemiBold.26ad6bf1.ttf);
}
body {
  margin: 0;
  margin-top: 30px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0A0A0A;
  color: white;
  text-align: center;
}

.subtext {
  font-size: 16px;
}

*{
  box-sizing: border-box;
}

.App>*{
  margin-bottom: 80px;
}


@media only screen and (max-width: 768px){
  .App>*{
    margin-bottom: 50px;
  }
}
p{font-size:24px;font-family:ManropeMedium;color:rgba(255,255,255,0.6);margin:0}@media only screen and (max-width: 768px){p{font-size:18px}}

.three-d-shadow{position:absolute;width:40px;height:100%;top:0}.three-d-shadow .skew-container{z-index:-1;height:50%;position:absolute;width:100%;overflow:hidden}.three-d-shadow .skew-container div{position:absolute;height:130%;width:100%}.three-d-shadow[direction='right']{left:calc(100% - 11px)}.three-d-shadow[direction='right'] .skew-container div{-webkit-transform-origin:left;transform-origin:left;background:linear-gradient(90deg, #403E41 18.19%, rgba(60,60,60,0) 101.82%)}.three-d-shadow[direction='right'] .skew-top-container{top:0}.three-d-shadow[direction='right'] .skew-top-container div{-webkit-transform:skewy(20deg);transform:skewy(20deg)}.three-d-shadow[direction='right'] .skew-bottom-container{bottom:0}.three-d-shadow[direction='right'] .skew-bottom-container div{-webkit-transform:skewy(-20deg);transform:skewy(-20deg);bottom:0}.three-d-shadow[direction='left']{right:calc(100% - 11px)}.three-d-shadow[direction='left'] .skew-container div{-webkit-transform-origin:right;transform-origin:right;background:linear-gradient(270.44deg, #3E3D40 .41%, rgba(62,61,64,0) 99.66%)}.three-d-shadow[direction='left'] .skew-top-container{bottom:0}.three-d-shadow[direction='left'] .skew-top-container div{bottom:0;-webkit-transform:skewy(20deg);transform:skewy(20deg)}.three-d-shadow[direction='left'] .skew-bottom-container{top:0}.three-d-shadow[direction='left'] .skew-bottom-container div{-webkit-transform:skewy(-20deg);transform:skewy(-20deg);top:0}@media only screen and (max-width: 900px){.three-d-shadow{display:none}}

.defi-comp-box{height:310px;width:100%;padding:40px 0;background-color:#272628;border-radius:13px;display:flex;flex-direction:column;margin:0 auto;position:relative}.defi-comp-box .icon-container{flex:1 1;align-items:center;justify-content:center;display:flex}.defi-comp-box .icon-container img{height:160px;width:auto}.defi-comp-box p{color:white}

.section-padding{padding:0 5%;margin:0 auto}@media only screen and (min-width: 1051px){.section-padding{padding:0;width:1020px}}

h2{font-size:62px;font-family:EuclidBold;margin:0}h2[small="true"]{font-size:55px}@media only screen and (max-width: 768px){h2{font-size:60px}h2[small="true"]{font-size:35px}}

.defi-section h2{margin-bottom:25px}.defi-section p{width:80%;margin:0 auto}.defi-section .boxes-container{margin-top:50px;display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:40px}@media only screen and (max-width: 900px){.defi-section .boxes-container{display:flex;flex-wrap:wrap;justify-content:center;grid-gap:unset}.defi-section .boxes-container>*{width:47%;margin-bottom:25px}}@media only screen and (max-width: 600px){.defi-section p{width:100%}.defi-section .boxes-container{display:block}.defi-section .boxes-container>*{width:100%;max-width:300px;margin-bottom:25px}}

.hero-section{padding-top:40px;margin-bottom:0}.hero-section h2{margin-bottom:20px}@media only screen and (max-width: 500px){.hero-section{text-align:left}}

.image-with-Bg{position:relative;background-color:#C4C4C4;width:100%}.image-with-Bg .image{position:absolute;top:0;left:0;bottom:0;right:0;display:flex;justify-content:center;align-items:center;overflow:hidden}.image-with-Bg .image img{width:100%;height:auto}

.step-box{display:flex;height:75px;width:calc(100% - 10px);background:linear-gradient(to right, #A54CFF 0%, #8046dd 50%, #4c85ff 100%);background-size:300% !important;display:flex;position:relative;align-items:center}.step-box:hover{animation-name:gradient;-webkit-animation-name:gradient;animation-duration:1s;-webkit-animation-duration:1s;-webkit-animation-direction:alternate;animation-direction:alternate;animation-iteration-count:infinite;-webkit-animation-iteration-count:infinite;animation-fill-mode:forwards;-webkit-animation-fill-mode:forwards}.step-box::before,.step-box::after{content:'';position:absolute;background-color:#A099B3}.step-box::before{width:100%;height:10px;top:100%;-webkit-transform:skew(45deg);transform:skew(45deg);-webkit-transform-origin:top;transform-origin:top}.step-box::after{height:100%;width:10px;left:100%;-webkit-transform:skew(0, 45deg);transform:skew(0, 45deg);-webkit-transform-origin:left;transform-origin:left;border-bottom:1px solid black}.step-box *{color:white !important;font-size:19px}.step-box .number{width:70px;display:flex;align-items:center;justify-content:center}@-webkit-keyframes gradient{0%{background-position:0% 50%}100%{background-position:100%}}@keyframes gradient{0%{background-position:0% 50%}100%{background-position:100%}}

.nft-steps-section{background:url(/static/media/dottedBg.b2a6cad4.jpg);padding:100px 0;background-position:center;background-size:cover}.nft-steps-section .container{display:flex;justify-content:space-between;align-items:center}.nft-steps-section .container .boxes-container{width:47%}.nft-steps-section .container .boxes-container>*{margin-bottom:40px}.nft-steps-section .container .boxes-container>*:last-child{margin:0}.nft-steps-section .container .image-container{width:42%;position:relative;margin-right:20px}.nft-steps-section .container .image-container .image-inner{background-color:#9D9D9D;padding:3px;position:absolute;width:100%;height:100%;top:0;left:0}.nft-steps-section .container .image-container .image-inner:first-child{position:relative}.nft-steps-section .container .image-container .image-inner:nth-child(2){top:10px;left:10px}.nft-steps-section .container .image-container .image-inner:nth-child(1){top:20px;left:20px}@media only screen and (max-width: 768px){.nft-steps-section .container{flex-direction:column}.nft-steps-section .container .boxes-container{width:100%;order:1}.nft-steps-section .container .image-container{width:calc(100% - 20px);margin-bottom:50px}}

.roadmap-section h2{margin-bottom:25px}.roadmap-section .map-container{position:relative}.roadmap-section .map-container .line{height:100%;position:absolute;left:50%;z-index:-1;background-color:rgba(255,255,255,0.6);width:2px}.roadmap-section .map-container .text-container{padding:8px 0;background-color:#0A0A0A;z-index:10;margin-bottom:35px}

.MuiButtonBase-root{margin-top:30px !important;-webkit-appearance:none !important;background:-webkit-gradient(to right, #756cff 0%, #70ec9d 50%, #70b1d1 100%);background:linear-gradient(to right, #756cff 0%, #70ec9d 50%, #70b1d1 100%);background-size:500% !important;border:none !important;border-radius:5rem !important;justify-content:center !important;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.15) !important;color:#fff !important;cursor:pointer !important;font:1.3em Raleway, sans-serif !important;-webkit-font-smoothing:antialiased !important;-moz-osx-font-smoothing:grayscale !important;height:4rem !important;letter-spacing:.05em !important;outline:none !important;-webkit-tap-highlight-color:transparent !important;-webkit-user-select:none !important;-ms-user-select:none !important;user-select:none !important;width:15rem !important}.MuiButtonBase-root:hover{animation-name:gradient !important;-webkit-animation-name:gradient !important;animation-duration:2s !important;-webkit-animation-duration:2s !important;animation-iteration-count:1 !important;-webkit-animation-iteration-count:1 !important;animation-fill-mode:forwards !important;-webkit-animation-fill-mode:forwards !important}@-webkit-keyframes gradient{0%{background-position:0% 50%}100%{background-position:100%}}@keyframes gradient{0%{background-position:0% 50%}100%{background-position:100%}}

footer{color:rgba(255,255,255,0.521);margin-bottom:30px !important;margin-top:100px}footer a{color:rgba(255,255,255,0.521)}

